@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap');

:root{
    --cyan: #0090a5;
    --bg-section: rgba(250,250,250,.3);
}
* {
    font-family: 'Nunito', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    border: none;
    text-decoration: none;
    transition: all .1s linear;
}
html {
    font-size: 62.5%;
    overflow-x: hidden;
}
html::-webkit-scrollbar{
    width:.7rem;
}
  
html::-webkit-scrollbar-track{
    background:#fff;
}
  
html::-webkit-scrollbar-thumb{
    background:var(--cyan);
}
  
body{
    background:#b3b3b3;
}
section{
    padding:10rem 9%;
    min-height: 100vh;
    width: 100%;
}
  
.heading{
    text-align: center;
    padding-bottom: 2rem;
    font-size: 5rem;
    text-transform: uppercase;
    color:#222;
    span{
        text-transform: uppercase;
        color:var(--cyan);
    }
}
  
.btn{
    display: inline-block;
    margin-top: 1rem;
    padding:.8rem 3rem;
    background:#222;
    color:#fff;
    font-size: 1.7rem;
    cursor: pointer;
    border-radius: .2rem;
    &:hover{
        background:var(--cyan);
        letter-spacing: .2rem;
    }
}
